am, speed is necessary."),_c('br'),_vm._v("\nIf you don't get your leo banned, it's not a problem, but basically turn1 and CC team players tend to ban more leo."),_c('br'),_vm._v("\nSo, to win in such a situation, you need a monster with high speed.")])]),_vm._v(" "),_c('div',{staticClass:"headline font-weight-bold my-6",staticStyle:{"font-family":"Anton!important"},attrs:{"id":"2"}},[_vm._v("What's important in RTA")]),_vm._v(" "),_c('v-row',{staticClass:"main-banner-area rounded-xl subtitle-1 white--text pa-4 mb-4",attrs:{"justify":"center"}},[_vm._v("\n      Here are three important things to remember about rta"),_c('br'),_c('br'),_vm._v("\nFirst"),_c('br'),_vm._v("\nStrengthen your mentality"),_c('br'),_vm._v("\nrune and monster are important, but the most important thing is mentality"),_c('br'),_vm._v("\nBeing able to pick and bang calmly will raise your rank"),_c('br'),_vm._v("\nIt's not easy to strengthen your mentality, but my recommendation is streaming"),_c('br'),_vm._v("\nStreaming and talking with your audience is much less stressful than playing alone"),_c('br'),_c('br'),_vm._v("\n\nSecond"),_c('br'),_vm._v("\nAnalyze your losses"),_c('br'),_vm._v("\nAnalyze what went wrong so that the next time you play against a similar opponent, you'll lose in a different way than the last time, and you'll be fine"),_c('br'),_vm._v("\nIt's important to try different things to find out what the answer is"),_c('br'),_vm._v("\nThere are many similar enemies, so if you don't analyze, you'll end up losing the same way"),_c('br'),_c('br'),_vm._v("\n\nThird"),_c('br'),_vm._v("\nFind players who have the same picks as you and add them to your favorites"),_c('br'),_vm._v("\nYou can learn a lot from other players about their picks, bans and drafts that you might not have thought of on your own"),_c('br'),_vm._v("\nIt would be nice to be able to ask that player questions if possible"),_c('br'),_vm._v("\nEven if you are making similar picks, a slight difference in the order of your picks can change the outcome"),_c('br'),_vm._v("\nEven if you use the monster from the second pick in the last pick, the enemy's picks will change, and you can reach a higher rank by learning such a strategy"),_c('br')]),_vm._v(" "),_c('div',{staticClass:"headline font-weight-bold my-6",staticStyle:{"font-family":"Anton!important"},attrs:{"id":"3"}},[_vm._v("Recommendations for rta beginners")]),_vm._v(" "),_c('v-row',{staticClass:"main-banner-area rounded-xl subtitle-1 white--text pa-4 mb-4",attrs:{"justify":"center"}}),_vm._v(" "),_c('div',{staticClass:"headline font-weight-bold my-6",staticStyle:{"font-family":"Anton!important"},attrs:{"id":"4"}},[_vm._v("RTA TIER LIST B ~ SSS RANK 2025/01ver")]),_vm._v(" "),_c('v-row',{staticClass:"main-banner-area rounded-xl subtitle-1 white--text pa-4 mb-4",attrs:{"justify":"center"}},[_c('v-col',{attrs:{"cols":"6"}},[_c('v-card',{staticStyle:{"height":"100%"},attrs:{"hover":"","ripple":"","href":"https://youtu.be/xvyffdfhIsc","target":"_blank"}},[_c('v-img',{staticStyle:{"position":"relative"},attrs:{"src":"article4.png"}})],1)],1),_vm._v(" "),_c('v-col',{attrs:{"cols":"12"}},[_c('span',{staticClass:"font-weight-bold"},[_vm._v("B-rank monsters")]),_c('br'),_vm._v("\n\nAs for akroma"),_c('br'),_vm._v("\nThis monster became more versatile thanks to bp that added gauge increase, making it possible to interrupt the opponent."),_c('br'),_vm._v("\nFor example, when attacked with AoE or control skills, the gauge increase allows it to interrupt and stop the opponent’s moves, which is a significant strength."),_c('br'),_vm._v("\nThe situation where it would continuously take hits without response has been greatly improved, broadening its strategic potential."),_c('br'),_vm._v("\nFor these reasons, it’s rated as a strong B-rank monster this season."),_c('br'),_c('br'),_vm._v("\n\nAs for Elenoa"),_c('br'),_vm._v("\nThis monster isn’t picked often, but its power is extremely strong when it does pick."),_c('br'),_vm._v("\nIn addition 