 last time, and you'll be fine"),_c('br'),_vm._v("\nIt's important to try different things to find out what the answer is"),_c('br'),_vm._v("\nThere are many similar enemies, so if you don't analyze, you'll end up losing the same way"),_c('br'),_c('br'),_vm._v("\n\nThird"),_c('br'),_vm._v("\nFind players who have the same picks as you and add them to your favorites"),_c('br'),_vm._v("\nYou can learn a lot from other players about their picks, bans and drafts that you might not have thought of on your own"),_c('br'),_vm._v("\nIt would be nice to be able to ask that player questions if possible"),_c('br'),_vm._v("\nEven if you are making similar picks, a slight difference in the order of your picks can change the outcome"),_c('br'),_vm._v("\nEven if you use the monster from the second pick in the last pick, the enemy's picks will change, and you can reach a higher rank by learning such a strategy"),_c('br')]),_vm._v(" "),_c('div',{staticClass:"headline font-weight-bold my-6",staticStyle:{"font-family":"Anton!important"},attrs:{"id":"3"}},[_vm._v("Recommendations for rta beginners")]),_vm._v(" "),_c('v-row',{staticClass:"main-banner-area rounded-xl subtitle-1 white--text pa-4 mb-4",attrs:{"justify":"center"}}),_vm._v(" "),_c('div',{staticClass:"headline font-weight-bold my-6",staticStyle:{"font-family":"Anton!important"},attrs:{"id":"4"}},[_vm._v("RTA TIER LIST B ~ SSS RANK 2025/01ver")]),_vm._v(" "),_c('v-row',{staticClass:"main-banner-area rounded-xl subtitle-1 white--text pa-4 mb-4",attrs:{"justify":"center"}},[_c('v-col',{attrs:{"cols":"12"}},[_c('v-card',{staticStyle:{"height":"100%"},attrs:{"hover":"","ripple":"","href":"https://youtu.be/xvyffdfhIsc","target":"_blank"}},[_c('v-img',{staticStyle:{"position":"relative"},attrs:{"src":"article4.png"}})],1)],1),_vm._v(" "),_c('span',{staticClass:"font-weight-bold"},[_vm._v("B-rank monsters")]),_c('br'),_vm._v("\n\nAs for akroma"),_c('br'),_vm._v("\nThis monster became more versatile thanks to bp that added gauge increase, making it possible to interrupt the opponent."),_c('br'),_vm._v("\nFor example, when attacked with AoE or control skills, the gauge increase allows it to interrupt and stop the opponent’s moves, which is a significant strength."),_c('br'),_vm._v("\nThe situation where it would continuously take hits without response has been greatly improved, broadening its strategic potential."),_c('br'),_vm._v("\nFor these reasons, it’s rated as a strong B-rank monster this season."),_c('br'),_c('br'),_vm._v("\n\nAs for Elenoa"),_c('br'),_vm._v("\nThis monster isn’t picked often, but its power is extremely strong when it does pick."),_c('br'),_vm._v("\nIn addition to skill 2’s cooldown reduction and skill 3’s ability to inflict multiple harmful effects on opponents, it can also equip Despair Runes to aim for AoE stuns, making it particularly effective against enemies without immunity."),_c('br'),_vm._v("\nEven if the opponent is immune, she becomes very dangerous after the opponent removes it with other monsters"),_c('br'),_c('br'),_vm._v("\n\nAs for Wind Monkey"),_c('br'),_vm._v("\nThe frequency of this monster’s use has increased following its bp"),_c('br'),_vm._v("\nPreviously, it had a skill that increased its gauge when attacked while unable to act, but now it has been buffed to include a stripping effect."),_c('br'),_vm._v("\nInitially, I wasn’t sure how to interpret the change, but after using him, I found it more user-friendly than before."),_c('br'),_vm._v("\nIts ability to strip immunity and follow up with a stun is particularly useful against trending immunity monsters."),_c('br'),_vm._v("\nWhile it’s not an overwhelming meta pick, it’s balanced and strong enough as rank-b"),_c('br'),_c('br'),_vm._v("\n\nAs for Chow"),_c('br'),_vm._v("\nThis monster was once considered a disappointment but is now an essential pick."),_c('br'),_vm._v("\nThe main reason is its ability to remove two debuffs, such as stuns, defense breaks, silences, or bombs, allowing it to take action and shift the flow of battle."),_c('br'),_vm._v("\nBy pairing it with monsters like Giou or Velajuel to apply defense break, it can follow up to eliminate opponents like Belial."),_c('br'),_vm._v("\nhis stats can be adjusted to increase its damage output, making it highly versatile."),_c('br'),_vm._v("\nThus, it’s placed in the B-rank."),_c('br'),_c('br'),_vm._v(" "),_c('span',{staticClass:"font-weight-bold"},[_vm._v("A-rank monsters")]),_c('br'),_vm._v("\n\nAs for Bella"),_c('br'),_vm._v("\nThis monster doesn’t pick frequently but stands out for its strength when it does."),_c('br'),_vm._v("\nThough Bella lacks durability, making it susceptible to counters, allowing it free movement can be dangerous."),_c('br'),_vm._v("\nIn particular, combinations of additional damage and Violent Runes can result massive damage."),_c('br'),_vm._v("\nno durability monsters can sometimes be taken out quickly, and Bella is often paired with other strong monsters, making it a difficult opponent to face."),_c('br'),_c('br'),_vm._v("\n\nAs for Mei Hou Wang"),_c('br'),_vm._v("\nA beloved pick for many players, this monster is versatile, effective in both turn1 and turn2"),_c('br'),_vm._v("\nIt’s particularly effective against meta monsters like Sierra and Gianna"),_c('br'),_vm._v("\nits stun immunity alone is highly valuable, allowing it to perform consistently in early stages, while its potential to capitalize on defense breaks and Violent Rune procs makes it a late-game powerhouse."),_c('br'),_c('br'),_vm._v("\n\nAs for Jose"),_c('br'),_vm._v("\nThis monster received so useful buff this season, with its provoke extending to two turns."),_c('br'),_vm._v("\nThe provoke limits the opponent’s actions, making it particularly troublesome to face."),_c('br'),_vm._v("\nIts passive skill, which allows it to act when she stunned, has long been appreciated as a counter to Despair Runes or monsters like Gianna that aim to stun."),_c('br'),_vm._v("\nBuilding its defense makes it effective against threats like Sonia"),_c('br'),_c('br'),_vm._v("\n\nAs for Seimei"),_c('br'),_vm._v("\nWith a passive skill that limits the duration of the opponent’s buffs to just one turn, she is incredibly powerful in the current RTA meta."),_c('br'),_vm._v("\nIt nullifies immunity and beneficial effects from monsters like Velajuel, Kovarch, and Shizuka"),_c('br'),_vm._v("\nIts ability to shorten the duration of debuffs also reduces the impact of effects like defense breaks,"),_c('br'),_c('br'),_vm._v(" "),_c('span',{staticClass:"font-weight-bold"},[_vm._v("S-Rank Monsters")]),_c('br'),_vm._v("\n\nAs for Julian"),_c('br'),_vm._v("\nThis monster received a nerf in BP this season, significantly reducing its stun rate compared to before.  Situations where it used to stuns almost without fail now have visibly lower success rates."),_c('br'),_vm._v("\nLast season, Julian was rated as a Triple-S Rank monster, but due to this adjustment, it has been placed in the S-Rank tier. That said, its performance remains exceptionally high."),_c('br'),_vm._v("\nIts ability to remove debuffs while dealing high damage is still intact, making it one of the more challenging monsters to take down. While it’s possible to defeat Julian with tesarion or Raki, it’s not a simple task, especially considering the support from surrounding monsters."),_c('br'),_vm._v("\nEven as it stands, Julian is powerful enough that another nerf wouldn’t be surprising."),_c('br'),_c('br'),_vm._v("\n\nAs for Miles"),_c('br'),_vm._v("\nAlthough Miles pick rate has decreased compared to previous seasons, Its ability to deal consistent damage without relying on critical hits allows it to maintain high durability while continuously dishing out damage"),_c('br'),_vm._v("\nI recommend using one-shot monsters like Belial to counter it effectively."),_c('br'),_vm._v("\nHowever, if that isn’t an option, it can be extremely difficult to take down, which is one of its most troublesome aspects"),_c('br'),_vm._v("\nit is possible in the future that he will be nerfed"),_c('br'),_c('br'),_vm._v("\n\nAs for Zima"),_c('br'),_vm._v("\nThis monster has newly entered the S-Rank tier this season."),_c('br'),_vm._v("\nIn previous seasons, Zima had a low pick rate and wasn’t ranked, but bp have completely changed its evaluation. The standout change is its ability to inflict a 2-turn silence effect on a selected target while performing an AoE attack, which is incredibly powerful."),_c('br'),_vm._v("\nThis skill allows her to greatly restrict the opponent’s actions right from the start, putting them in a state where they can’t use their skills."),_c('br'),_vm._v("\nWhile her pick rate isn’t very high yet, it’s a troublesome presence when it does appear. Some top players have already started incorporating her into their strategies, so its pick rate may increase further in the upcoming seasons."),_c('br'),_c('br'),_vm._v(" "),_c('span',{staticClass:"font-weight-bold"},[_vm._v("SS-Rank monsters")]),_c('br'),_vm._v("\n\nAs for Water Twin Angel"),_c('br'),_vm._v("\nThis monster continues to be a frequent pick this season"),_c('br'),_vm._v("\nShe has highly versatile skills, including healing, shielding, AoE stuns, stripping buffs, and defense breaks. Although her AoE stuns and stripping + defense break are not guaranteed, I feel the percentage so high"),_c('br'),_vm._v("  \nFor instance, if she manages to strip buffs and give defense break in her first move, it’s not uncommon for her high-damage teammates to follow up and deal massive damage."),_c('br'),_vm._v("\nfor example julian, ashourl"),_c('br'),_vm._v("\nWhile she disrupts the enemy team, her allies can freely act, making it very difficult to recover once she gains momentum."),_c('br'),_vm._v("\nWater Twin Angel remains a top-tier monster, just as she was last season."),_c('br'),_c('br'),_vm._v("\n\nAs for Velajuel"),_c('br'),_vm._v("\nVelajuel is still one of the most popular first picks among many players this season."),_c('br'),_vm._v("\nHis skill2 short cooldown times are a key strength, as even if his cooldowns are extended, he can quickly use his skills again. Skill 2, in particular, has a 2-turn cooltime and can be used repeatedly when paired with Violent runes, allowing him to contribute both turn1 and turn2"),_c('br'),_vm._v("\nhe supports his allies with healing and buffs while dealing damage with defense breaks, making him a versatile pick."),_c('br'),_vm._v("\nTop players often include Velajuel in their teams, combining him with monsters like Chandra for highly effective strategies. His high pick rate and consistent performance make him an indispensable presence this season."),_c('br'),_c('br'),_vm._v("\n\nAs for Jeongnum"),_c('br'),_vm._v("\nthis bp has made Jeongnu a highly notable monster."),_c('br'),_vm._v("\nNowadays, he’s frequently seen in specific team compositions, such as the Oliver + Jeongnum + Zima setup, which has proven to be extremely powerful."),_c('br'),_vm._v("\nhis strengths lie in his ability to heal all allies and his Skill 3, which provides AoE stuns and gauge increase. Additionally, Skill 2 deals fixed damage, ensuring reliable damage output against any target, making him an excellent choice for consistent chip damage."),_c('br'),_vm._v("\nHis fast skill rotation and ability to act before opponents add to his appeal."),_c('br'),_c('br'),_vm._v(" "),_c('span',{staticClass:"font-weight-bold"},[_vm._v("SSS-Rank Monsters")]),_c('br'),_vm._v("\n\nAs for Ragdoll"),_c('br'),_vm._v("\nhe continues to be an incredibly strong monster."),_c('br'),_vm._v("\nAt g3, he is frequently picked. His passive is particularly troublesome, disrupting the opponent’s turn management through gauge increases."),_c('br'),_vm._v("\nWhen attempting to execute a series of skill combos, Ragdoll can interrupt and turn the tide of the battle."),_c('br'),_vm._v("\nhe has consistently adapted to the meta, proving his worth as an SSS-Rank monster this season as well."),_c('br'),_c('br'),_vm._v("\n\nAs for Giana"),_c('br'),_vm._v("\nAlthough Giana’s stun rate was reduced from 100% to 85% this season, she remains a highly powerful monster."),_c('br'),_vm._v("\nThe slight nerf is noticeable at times, but her ability to control matches is still intact. Her skill set and versatility allow her to remain relevant in the meta, and maybe she will be nerfed even more"),_c('br'),_c('br'),_vm._v("\n\nAs for Berghild"),_c('br'),_vm._v("\nAmong Light monsters, Berghild is one of the strongest in the current meta."),_c('br'),_vm._v("\nHer defining trait is her gauge-increaseing ability, which enables her to disrupt opponents by taking the first turn and chaining stuns and defense breaks."),_c('br'),_vm._v("\nPlus she doesn't stun and therefore can't be stopped easily"),_c('br'),_vm._v("\nI see a lot of swift builds, but some players are using them in vio, so be careful"),_c('br'),_vm._v("\nSince she hasn’t received a nerf yet, there’s a possibility of it in the future."),_c('br'),_c('br'),_vm._v("\n\nAs for Kovarch"),_c('br'),_vm._v("\nshe remains a formidable monster in the current meta."),_c('br'),_vm._v("\nHer immunity-granting, attack buffing, and gauge-increasing capabilities make her an outstanding support."),_c('br'),_vm._v("\nSkill 2, which shortens cooldowns and heals, is particularly powerful"),_c('br'),_vm._v("\nCompared to velajuel, she is more useful in some cases because she can continue to use immunity"),_c('br'),_vm._v("\nStripping or defense-ignoring attacks are necessary to defeat her, but she remains difficult to counter effectively."),_c('br'),_c('br'),_vm._v("\n\nConclusion"),_c('br'),_vm._v("\nThat wraps up the current RTA tier list. It will be interesting to see how this list changes with the BP adjustments in January.\n      ")],1)],_vm._v(" "),[_c('adsbygoogle',{attrs:{"ad-slot":"9249275840"}})]],2)],1)
